@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
nav {
  background-color: var(--blue);
  display: grid;
  grid-template-columns: repeat(3, 33.3%);
  height: 4.5rem;
  position: relative;
}

.fa-bars {
  color: var(--light-blue);
  height: 100%;
  margin-left: 1.7rem;
}

.brand {
  display: flex;
  justify-content: center;
  align-items: center;
}

.brand img {
  height: 3rem;
}

/*  */

.nav-options {
  visibility: hidden;
  position: absolute;
  display: grid;
  grid-template-columns: 85% 15%;
  background-color: var(--blue);
  z-index: 2;
  width: 100%;
  height: 4.5rem;
}

.options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 1rem;
}

.options a {
  text-decoration: none;
  color: var(--white);
  font-size: 1.2rem;
}

.initial-option {
  position: relative;
}

.initial-option span {
  position: absolute;
  display: block;
  left: 50%;
  width: 0.5rem;
  height: 0.5rem;
  margin-top: 0.3rem;
  border-radius: 100%;
  background-color: var(--light-blue);
}

.closeMenu {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--light-blue);
}

@media screen and (min-width: 768px) {
  nav {
    grid-template-columns: repeat(2, 50%);
    height: 5.5rem;
  }

  .burger-menu {
    display: none;
  }

  .closeMenu {
    display: none;
  }

  .brand {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    margin: 0 0 0 3rem;
    justify-content: flex-start;
  }

  .nav-options {
    visibility: visible;
    position: static;
    position: initial;
    grid-column-start: 2;
    grid-column-end: 3;
    margin: 0 2rem 0 0;
    display: flex;
    background-color: none;
    width: auto;
    height: 100%;
  }

  .options {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0;
    width: 100%;
    font-weight: 500;
    letter-spacing: 0.8px;
  }
}

@media screen and (min-width: 1152px) {
  .brand {
    margin: 0 0 0 4.5rem;
  }

  .nav-options {
    margin: 0 4.5rem 0 0;
  }

  .options {
    justify-content: flex-start;
  }

  .options > a {
    margin: 0 20% 0 0;
  }
}

@media screen and (min-width: 1280px) {
  .options > a {
    margin: 0 25% 0 0;
  }
}

.media {
  display: flex;
  justify-content: center;
  height: 2rem;
  align-items: center;
  margin: 1rem 0;
}

.media > a {
  margin: 0 0.8rem;
  color: var(--white);
}

/*  */

.media-footer {
  display: flex;
  justify-content: center;
  height: 2rem;
  align-items: center;
  margin: 1rem 0;
}

.media-footer > a {
  margin: 0 0.8rem;
  color: var(--white);
}

@media screen and (min-width: 768px) {
  .media {
    justify-content: flex-start;
    margin: 1rem 0;
  }

  .media > a {
    margin: 0 2rem 0 0;
  }
}

.introContainer {
  background-color: var(--blue);
  background-image: url("/media/wave.svg");
  background-repeat: no-repeat;
  background-position: bottom;
  padding-bottom: 5rem;
}

.infoUser {
  display: grid;
  justify-content: center;
  padding-bottom: 1rem;
}

.user {
  margin: 1rem 0;
  font-size: 2.8rem;
  font-weight: 500;
  color: var(--white);
}

.line {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1rem;
}

.line > span {
  display: block;
  width: 3.5rem;
  height: 0.4rem;
  background-color: var(--light-blue);
}

.information {
  margin: 0 1.7rem;
}

.infoTitle {
  color: var(--white);
  width: 90%;
  font-size: 1.2rem;
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-bottom: 0.8rem;
}

.infoDescription {
  text-align: justify;
  width: 80%;
  color: var(--gray);
  font-size: 0.9rem;
  font-weight: 300;
}

@media screen and (min-width: 768px) {
  .introContainer {
    padding-bottom: 6rem;
  }

  .info {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    margin: 0 3rem;
  }

  .infoUser {
    display: grid;
    justify-content: flex-start;
    padding: 2rem 0;
  }

  .user {
    font-size: 3rem;
  }

  .line {
    justify-content: flex-start;
  }

  .line span {
    width: 4rem;
  }

  .information {
    padding: 2rem 0;
    margin: 0;
    display: grid;
    align-content: center;
  }

  .infoTitle {
    width: 90%;
    font-size: 1.6rem;
    margin-bottom: 1.3rem;
  }

  .infoDescription {
    font-size: 1rem;
  }
}

@media screen and (min-width: 1152px) {
  .introContainer {
    background-size: 0%;
  }

  .info {
    margin: 0 4.5rem;
  }

  .infoUser {
    padding: 3rem 0;
  }
}

.work-container {
  background-color: var(--dark-blue);
  padding-bottom: 1rem;
}

.work-info {
  display: grid;
  grid-template-columns: repeat(2, 50%);
  padding: 1.5rem 0 0 0;
  margin: 0 1.7rem;
}

.work-title p {
  margin: 0 0 0.3rem 0;
  font-size: 0.7rem;
  color: var(--gray);
  letter-spacing: 0;
}

.work-title {
  color: var(--white);
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: 0.5rem;

  /* text-align: right; */
}

.work-description {
  color: var(--gray);
  font-size: 0.9rem;
  font-weight: 300;
  /* text-align: justify; */
}

.work-image {
  display: flex;
  justify-content: flex-end;
  /* justify-content: flex-start; */
  align-items: center;
}

.work-image img {
  height: 7rem;
}

.projects-container {
  margin: 4rem 1.7rem;
}

.project {
  display: grid;
  cursor: pointer;
  grid-template-columns: repeat(8, 12.5%);
  grid-template-rows: repeat(10, 10%);
  height: 8.5rem;
  margin: 5rem 0 6rem 0;
}

.xdelta {
  background-image: url("/media/projects/xdelta.png");
}

.mxcoin {
  background-image: url("/media/projects/mxcoin.png");
}

.aedificium {
  background-image: url("/media/projects/aedificium.png");
}

.monster {
  background-image: url("/media/projects/monster.png");
}

.project-presentation {
  display: flex;
  grid-column-start: 1;
  grid-column-end: 7;
  grid-row-start: 1;
  grid-row-end: 11;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.project-presentation > span {
  display: block;
  background-color: var(--blue-project);
  width: 100%;
}

.project-description {
  grid-column-start: 3;
  grid-column-end: 9;
  grid-row-start: 3;
  grid-row-end: 9;
  display: grid;
  background-color: var(--blue);
  align-content: center;
}

.project-title {
  display: flex;
  align-items: center;
  color: var(--white);
  height: 1.5rem;
  margin: 0 0.9rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.project-text {
  color: var(--gray);
  margin: 0 0.9rem;
  font-size: 0.8rem;
}

.project-tools {
  visibility: hidden;
}

@media screen and (min-width: 768px) {
  .work-container {
    padding-bottom: 3rem;
  }

  .work-info {
    margin: 0 3rem;
  }

  .work-title p {
    font-size: 0.85rem;
  }

  .work-title {
    font-size: 1.6rem;
    margin-bottom: 0.5rem;
  }

  .work-description {
    font-size: 1rem;
  }

  .work-image img {
    height: 9rem;
  }

  .projects-container {
    margin: 3rem 3rem;
    display: grid;
    justify-content: center;
  }

  .project {
    height: 16rem;
    width: 35rem;
    margin: 5rem 0 6rem 0;
  }

  .project-presentation span {
    display: block;
    background-color: var(--blue-project);
    width: 100%;
  }

  .project-description {
    grid-column-start: 4;
  }

  .project-title {
    height: 1.5rem;
    margin: 0 1.5rem;
    font-size: 1.3rem;
  }

  .project-text {
    margin: 0.5rem 1.5rem;
    font-size: 1rem;
  }
}

@media screen and (min-width: 1152px) {
  .work-container {
    padding: 1.8rem 0;
  }

  .work-info {
    margin: 0 4.5rem;
  }

  .work-title {
    margin-bottom: 1rem;
  }

  .projects-container {
    margin: 0 4.5rem;
  }

  .project {
    height: 20rem;
    width: 45rem;
  }

  .project-presentation > span:hover {
    opacity: 0;
  }

  .project-description {
    grid-row-start: 3;
    grid-row-end: 8;
  }

  .project-tools {
    visibility: visible;
    grid-row-start: 8;
    grid-row-end: 11;
    grid-column-start: 7;
    grid-column-end: 9;
    color: var(--gray);
    font-size: 0.7rem;
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
    grid-template-rows: repeat(3, 33.3%);
  }

  .project-tools > div {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.contact-container {
  background-color: var(--blue);
  padding-bottom: 1rem;
}

.contact-info {
  padding: 1.5rem 0 0 0;
  margin: 0 1.7rem;
}

.contact-title p {
  margin: 0 0 0.3rem 0;
  font-size: 0.7rem;
  color: var(--gray);
  letter-spacing: 0;
}

.contact-title {
  color: var(--white);
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.contact-description {
  color: var(--gray);
  font-size: 0.9rem;
  font-weight: 300;
  text-align: justify;
  width: 80%;
}

.contact-description p {
  color: var(--light-blue);
  letter-spacing: 1.2px;
  font-size: 0.9rem;
  margin: 0;
  margin-top: 0.5rem;
}

.contact-form {
  margin: 3rem 4rem;
}

.form-section {
  margin: 3rem 0;
}

.form-section .form-label {
  color: var(--white);
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 1.2rem;
}

input {
  color: var(--white);
  background-color: transparent;
  border: 0;
  border-bottom: 1px solid var(--gray-input);
  width: 100%;
}

input:focus {
  outline: none;
}

.send-mail {
  display: flex;
  align-items: center;
}

.fa-paperclip {
  color: var(--gray-input);
  margin: 0 1.3rem;
  cursor: pointer;
}

.fa-arrow-right {
  color: var(--light-blue);
  cursor: pointer;
}

textarea#styled {
  color: var(--white);
  border: 0;
  border-bottom: 1px solid var(--gray-input);
  background-color: transparent;
  width: 100%;
  outline: red;
}

button {
  border: 0;
  background-color: transparent;
  margin: 0 1rem;
}

@media screen and (min-width: 768px) {
  .contact-container {
    padding-bottom: 3rem;
  }

  .contact-info {
    padding: 2rem 0 0 0;
    margin: 0 3rem;
  }

  .contact-title p {
    margin: 0 0 0.3rem 0;
    font-size: 0.85rem;
  }

  .contact-title {
    font-size: 1.6rem;
  }

  .contact-description {
    font-size: 1rem;
  }

  .contact-description p {
    font-size: 1rem;
  }

  .contact-form {
    margin: 3rem 10rem;
  }

  .form-section {
    margin: 4rem 0;
  }

  .form-section .form-label {
    font-size: 1rem;
    letter-spacing: 1px;
  }

  input {
    color: var(--white);
    background-color: transparent;
    border: 0;
    border-bottom: 1px solid var(--gray-input);
    width: 100%;
  }

  input:focus {
    outline: none;
  }

  .send-mail {
    display: flex;
    align-items: center;
  }

  .fa-paperclip {
    color: var(--gray-input);
    margin: 0 1.3rem;
    cursor: pointer;
  }

  .fa-arrow-right {
    color: var(--light-blue);
    cursor: pointer;
  }

  textarea#styled {
    color: var(--white);
    border: 0;
    border-bottom: 1px solid var(--gray-input);
    background-color: transparent;
    width: 100%;
  }
}

@media screen and (min-width: 1152px) {
  .contact-container {
    padding: 1.8rem 0 3rem 0;
    display: grid;
    grid-template-columns: repeat(2, 50%);
  }

  .contact-info {
    margin: 0 0 0 4.5rem;
  }

  .contact-title {
    font-size: 1.6rem;
    margin-bottom: 1rem;
  }

  .contact-description p {
    margin-top: 1rem;
  }

  .contact-form {
    margin: 0 8rem;
  }
}

.footer-container {
  background-color: var(--dark-blue);
  padding: 1rem 0;
}

.footer-content {
  display: grid;
  justify-content: center;
  align-items: center;
}

.footer-logo {
  display: flex;
  justify-content: center;
}

.footer-logo img {
  margin: 1.2rem 0;
  height: 3rem;
}

.footer-text {
  color: var(--white);
  font-weight: 500;
  letter-spacing: 0.5px;
  font-size: 0.9rem;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .footer-logo img {
    height: 4rem;
  }

  .footer-text {
    letter-spacing: 1px;
    font-size: 1rem;
  }
}

:root {
  --blue: #333647;
  --light-blue: #43fff4;
  --dark-blue: #252734;
  --blue-project: rgb(67, 255, 244, 0.5);
  --white: #f8f8f8;
  --gray: #c2c2c2;
  --gray-input: #717171;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  scroll-behavior: smooth;
}

@media screen and (min-width: 1280px) {
  body::-webkit-scrollbar {
    width: 5px; /* width of the entire scrollbar */
    background-color: #252734;
    background-color: var(--dark-blue);
  }

  body::-webkit-scrollbar-thumb {
    background-color: #43fff4;
    background-color: var(--light-blue); /* color of the scroll thumb */
    border-radius: 20px; /* roundness of the scroll thumb */
  }
}

